import React, { useState, useEffect } from "react";
import momentJalaali from "moment-jalaali";
import DatePicker from "react-datepicker2";
import notification from "helpers/notification";
import { Typeahead } from "react-bootstrap-typeahead";
// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  ButtonGroup,
  FormGroup,
  Button,
  Label,
  Input,
  CardTitle,
  Alert,
  Table,
} from "reactstrap";

// api
// import putDeathApi from "API/putDeath";
// import putAnimalSellApi from "API/putAnimalSell";
// import putSlaughterApi from "API/putSlaughter";
// import getActiveDisease from "API/disease/getActives";

import ApiPost from "API/post";
import ApiGet from "API/get";

// default values
// const illnessOptions = [
//   " ",
//   "",
//   "",
//   "",
//   "  ",
//   "",
//   "",
//   " ",
// ];

const formatter = new Intl.NumberFormat("sv-SE", {
  style: "decimal",
  currency: "SEK",
});

export default (props) => {
  const [mode, setMode] = useState(0);

  /* -------------------------------------------------------------------------- */
  /*                                   states                                   */
  /* -------------------------------------------------------------------------- */
  // sell states
  const [sellDate, setSellDate] = useState(momentJalaali());
  const [sellPrice, setSellPrice] = useState("");
  const [sellComment, setSellComment] = useState("");
  const [sellPriceError, setSellPriceError] = useState("");

  // slaughter states
  const [slaughterDate, setSlaughterDate] = useState(momentJalaali());
  const [weight, setWeight] = useState("");
  const [weightError, setWeightError] = useState("");

  // deatch states
  const [deathComment, setDeathComment] = useState("");
  const [deathCommentError, setDeathCommentError] = useState("");
  const [deathReasonError, setDeathReasonError] = useState("");
  const [deathDate, setDeathDate] = useState(momentJalaali());
  const [deathReason, setDeathReason] = useState("");

  const [noDiseaseFound, setNoDiseaseFound] = useState(false);
  const [diseaseArr, setDiseaseArr] = useState([]);
  const [selectedDisease, setSelectedDisease] = useState({});
  /* -------------------------------------------------------------------------- */
  /*                                  handlers                                  */
  /* -------------------------------------------------------------------------- */
  const handleDeathSubmit = () => {
    if (!deathReason) setDeathReasonError("    ");
    else if (deathReason === "" && !deathComment)
      setDeathCommentError("        ");
    else {
      const data = {
        entry: {
          comment: deathComment,
          reason: deathReason,
          createdAt: momentJalaali(),
          date: deathDate,
          key: props.animalKey,
          disease: deathReason === "" ? selectedDisease : undefined,
        },
        token: localStorage.artimal,
      };

      ApiPost("api/v0/exit/death", data)
        .then((res) => {
          if (deathReasonError) setDeathReasonError("");
          console.log(res);
          notification(res.result, "success");
          props.forceUpdate();
        })
        .catch((err) => {
          console.log("errrrrrrrrrrr", err);
          notification(err.error, "danger");
        });
    }
  };

  const handleSlaughterSubmit = () => {
    if (!weight) {
      setWeightError("   ");
    } else {
      const data = {
        entry: {
          weight: weight,
          date: deathDate,
          createdAt: momentJalaali(),
          key: props.animalKey,
        },
        token: localStorage.artimal,
      };

      ApiPost("api/v0/exit/slaughter", data)
        .then((res) => {
          if (sellPriceError) setSellPriceError("");
          console.log(res);
          notification(res.result, "success");
          props.forceUpdate();
        })
        .catch((err) => {
          console.log("errrrrrrrrrrr", err);
          notification(err.error, "danger");
        });
    }
  };

  const handleSellSubmit = () => {
    if (!sellPrice) {
      setSellPriceError("    ");
    } else {
      const data = {
        entry: {
          date: sellDate,
          price: sellPrice,
          comment: sellComment,
          key: props.animalKey,
        },
        token: localStorage.artimal,
      };

      // putAnimalSellApi(data)
      ApiPost("api/v0/exit/sell", data)
        .then((res) => {
          if (sellPriceError) setSellPriceError("");
          console.log(res);
          notification(res.result, "success");
          props.forceUpdate();
        })
        .catch((err) => {
          console.log("errrrrrrrrrrr", err);
          notification(err.error, "danger");
        });
    }
  };

  const handleSubmitDisease = (key) => {
    const item = diseaseArr.filter((obj) => obj._key === key);
    console.log("item is :::", item);

    setSelectedDisease(item[0]);
  };

  /* -------------------------------------------------------------------------- */
  /*                                   effect                                   */
  /* -------------------------------------------------------------------------- */
  useEffect(() => {
    setDeathComment("");
    setDeathCommentError("");
  }, [deathReason]);

  useEffect(() => {
    console.log(")))))))))))))", mode);
    if (mode === 2 && deathReason === "") {
      console.log("now its the time");

      ApiGet(`api/v0/disease/active/${props.animalKey}`)
        .then((res) => {
          // if (sellPriceError) setSellPriceError("");
          console.log(res);
          if (!res.result.length && noDiseaseFound === false)
            setNoDiseaseFound(true);
          if (res.result.length && noDiseaseFound === true)
            setNoDiseaseFound(false);

          setDiseaseArr(res.result);
          // notification(res.result, "success");
          // props.forceUpdate();
        })
        .catch((err) => {
          console.log("errrrrrrrrrrr", err);
          notification(err.error, "danger");
        });
    }
  }, [mode, deathReason, props.animalKey, noDiseaseFound]);

  /* -------------------------------------------------------------------------- */
  /*                                   return                                   */
  /* -------------------------------------------------------------------------- */
  return (
    <Card>
      <CardHeader>
        <CardTitle tag="h6"></CardTitle>
      </CardHeader>

      <CardBody>
        <ButtonGroup>
          <Button
            color={mode === 0 ? "primary" : "link"}
            type="button"
            onClick={() => setMode(0)}
          >
            
          </Button>
          <Button
            color={mode === 1 ? "primary" : "link"}
            type="button"
            onClick={() => setMode(1)}
          >
            
          </Button>
          <Button
            color={mode === 2 ? "primary" : "link"}
            type="button"
            onClick={() => setMode(2)}
          >
            
          </Button>
        </ButtonGroup>
        <hr />

        {mode === 0 ? (
          <>
            <FormGroup>
              <DatePicker
                isGregorian={false}
                timePicker={false}
                value={sellDate}
                onChange={(value) => setSellDate(value)}
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <FormGroup>
                <Input
                  placeholder=" "
                  value={formatter.format(sellPrice)}
                  onChange={(e) => {
                    setSellPrice(e.target.value.replace(/\s/g, ""));
                  }}
                  type="number"
                />
              </FormGroup>
              {sellPriceError ? (
                <p className="error-text-form">{sellPriceError}</p>
              ) : null}
            </FormGroup>

            <FormGroup>
              <Label for="exampleText"></Label>
              <Input
                type="textarea"
                value={sellComment}
                onChange={(e) => setSellComment(e.target.value)}
              />
            </FormGroup>
            <FormGroup>
              <Button type="button" color="primary" onClick={handleSellSubmit}>
                
              </Button>
            </FormGroup>
          </>
        ) : null}

        {mode === 1 ? (
          <>
            <FormGroup>
              <FormGroup>
                <DatePicker
                  isGregorian={false}
                  timePicker={false}
                  value={slaughterDate}
                  onChange={(value) => setSlaughterDate(value)}
                />
              </FormGroup>
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <FormGroup>
                <Input
                  placeholder=""
                  value={weight}
                  onChange={(e) => {
                    setWeight(e.target.value);
                  }}
                  type="number"
                />
              </FormGroup>
              {weightError ? (
                <p className="error-text-form">{weightError}</p>
              ) : null}
            </FormGroup>
            <FormGroup>
              <Button
                type="button"
                color="primary"
                onClick={handleSlaughterSubmit}
              >
                
              </Button>
            </FormGroup>
          </>
        ) : null}

        {mode === 2 ? (
          <>
            <FormGroup>
              <DatePicker
                isGregorian={false}
                timePicker={false}
                value={deathDate}
                onChange={(value) => setDeathDate(value)}
              />
            </FormGroup>
            <FormGroup>
              <Label> </Label>
              <FormGroup>
                <Input
                  className="select-input"
                  type="select"
                  onChange={(e) => {
                    setDeathReason(e.target.value);
                  }}
                  // isValid={deathReason}
                >
                  {!deathReason ? <option> </option> : null}
                  <option></option>
                  <option></option>
                </Input>
              </FormGroup>
              <p className="error-text-form">{deathReasonError}</p>
              {/* </FormGroup>
            {deathReason === "" ? (
              <FormGroup>
                <Label></Label>
                <Typeahead
                  className="typeahead"
                  id="vaccine-typeahead"
                  labelKey="name"
                  onChange={setIllness}
                  options={illnessOptions}
                  placeholder="   "
                  selected={illness}
                />
              </FormGroup>
            ) : null}
            <FormGroup> */}
              {Object.keys(selectedDisease).length ? (
                <Table>
                  <thead className="text-primary">
                    <tr>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        {momentJalaali(
                          selectedDisease.date,
                          "YYYY-M-DTHH:mm:ss.SSSZ"
                        ).format("jYYYY-jM-jD")}
                      </td>
                      <td>{selectedDisease.value}</td>
                      <td>
                        <Button
                          type="button"
                          // onClick={() => handleSubmitDisease(obj.d._key)}
                        >
                          
                        </Button>
                      </td>
                    </tr>
                  </tbody>
                </Table>
              ) : null}
              {noDiseaseFound ? (
                <Alert color="danger">
                          .   
                               
                </Alert>
              ) : null}
              {diseaseArr.length && !Object.keys(selectedDisease).length ? (
                <Table>
                  <thead className="text-primary">
                    <tr>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {diseaseArr.map((obj, i) => {
                      const createdAtJdate = momentJalaali(
                        obj.date,
                        "YYYY-M-DTHH:mm:ss.SSSZ"
                      ).format("jYYYY-jM-jD");
                      return (
                        <tr key={`disease${i}`}>
                          <td>{createdAtJdate}</td>
                          <td>{obj.value}</td>
                          <td>
                            <Button
                              type="button"
                              onClick={() => handleSubmitDisease(obj._key)}
                            >
                              
                            </Button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </Table>
              ) : null}

              <Label for="exampleText"></Label>
              <Input
                type="textarea"
                value={deathComment}
                onChange={(e) => setDeathComment(e.target.value)}
                disabled={
                  mode === 2 && deathReason === "" && noDiseaseFound
                }
              />
              <p className="error-text-form">{deathCommentError}</p>
            </FormGroup>
            <FormGroup>
              <Button
                type="button"
                color="primary"
                onClick={handleDeathSubmit}
                disabled={
                  mode === 2 &&
                  deathReason === "" &&
                  !Object.keys(selectedDisease).length
                }
              >
                
              </Button>
            </FormGroup>
          </>
        ) : null}
      </CardBody>
    </Card>
  );
};
